header{
    min-height: 100vh;
    padding-top: 0.7rem;
    overflow: hidden;
    /* background-image: url('../../assets/texture.jpg');
    background-size: cover; */
}

.header_container
{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============CTA=========== */
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap:1.2rem;
    gap:1.2rem;
    justify-content: center;
}

/* =======header socials============ */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left:1.5rem;
    background: var(--color-bg);
    padding:.5rem;
    border-radius: 10px;
    opacity: 60%;
    transition: var(--transition);
}
.header_socials:focus,
.header_socials:hover,
.header_socials:active{

    opacity: 100%;
}
.header_socials>a>svg:hover{
    transition: .4s ease-in-out;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.header_socials::before{
    content: '';
    width:1px;
    height: 2rem;
    background:var(--color-primary); 
}
.header_socials::after{
    content: '';
    width:1px;
    height: 2rem;
    background:var(--color-primary); 
}

.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 4rem;
    border-radius:12rem 12rem 0 0;
    padding: .5rem;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem ; */
    /* dosen't fit with current style or image */
}
.me>img{
    mix-blend-mode: screen;
    opacity: 90%;
    border-radius:12rem 12rem 0 0;
    transition: var(--transition);
}
.me>img:hover,
.me>img:active,
.me>img:focus{
    mix-blend-mode: normal;
    opacity: 100%;
    
}
/* =======scroll down============ */
.scroll_down{
    position:absolute;
    right:1rem;
    bottom: -8rem; 
    -webkit-transform: rotate(90deg); 
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

.scroll_down::after{
    content: '';
    width:1px;
    height: 2rem;
    background:var(--color-primary); 
}

