.contact_option-icon{
    font-size: 2rem;
    margin-bottom: 0.5rem;
}

.contact_option a{
    margin-top:0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* ===============Form ================= */

form{
    display: flex;
    grid-gap:1.5rem;
    gap:1.5rem;
    flex-direction: column;
    justify-content: center;
}

input,textarea{
    width:100%;
    padding:1.5rem;
    border-radius: 0.5rem;
    background-color: transparent;
    border:2px solid var(--color-primary-varient);
    resize: none;
    color:var(--color-white)
}
.input,textarea:active{
    border-color: var(--color-primary);
}
.contact_btn{
    margin-left: .2rem;
    padding:  auto 3rem;
    min-width: 10rem;
}

/* ============= MEDIA QUERIES (MEDIUM DEVICES) ============ */
@media only screen and (max-width:1024px) {

    .contact_container{
        grid-template-columns: 1fr;
        grid-gap:2rem;
        gap:2rem;
    }

 }
 /* ============= MEDIA QUERIES (SMALL DEVICES) ============ */
 @media only screen and (max-width:600px) {
     .contact_container{
        width: var(--container-width-sm);
        }

 }
footer{
    background: var(--color-primary);
    padding:3rem 0rem;
    text-align: center;
    font-size: .9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg);
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.footer_logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 2rem;
}
.footer_socials{
display: flex;
justify-content: center;
grid-gap:1rem;
gap:1rem;
margin-bottom: 1rem;
}

.footer_socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    font-size: 1.2rem;
    border:1px solid transparent;
}

.footer_socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer_copyright
{
    font-size: 1rem;
    margin-bottom:4rem;
    color: var(--color-bg);
}
