.night{

        --color-bg:#1f1f38;
        --color-bg-varient:#2c2c6c;
        --color-primary:#f0f06a;
        --color-primary-varient:rgba(255, 172, 77, 0.4);
        --color-white:#fff;
        --color-light:rgba(255,255,255,0.6);
        --transition:all 400ms ease;
        --container-width-lg:75%;
        --container-width-md:86%;
        --container-width-ms:90%;
    
}
.day{

        --color-bg:#e5e5ea;
        --color-bg-varient:#ffffff;
        --color-primary:#4db5ff;
        --color-primary-varient:rgba(77,181,255,0.4);
        --color-white:rgb(0, 0, 0);
        --color-light:rgba(117, 117, 117, 0.6);
        --transition:all 400ms ease;
        --container-width-lg:75%;
        --container-width-md:86%;
        --container-width-ms:90%;
    
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    background: var(--color-bg);
    color: var(--color-light);
    font-family: 'Poppins',quicksand,serif,arial;
    line-height: 1.7; 
    min-height: 100vh;
    background-repeat: repeat-x;

}
/* find background    */
/* general styles */
.container{
    width:var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 3rem;
    min-height: 100vh;
}

section > h2,
section>h5{
text-align: center;
color: var(--color-light);
}

section >h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
    transition: var(--transition);
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: var(--color-light);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.3rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border:1px solid var(--color-primary);
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-light);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit:cover;
}


/* =============MEDIA QUERIES (MEDIUM DEVICES)============ */
@media only screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* =============MEDIA QUERIES (SMALL DEVICES)============ */
@media only screen and (max-width:600px) {
    .container{
        width: var(--container-width-sm);
    }
    section>h2{
        margin-bottom: 2rem;
    }
}
