nav{
    background: rgba(0,0,0,0.3);
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom:2rem;
    display: flex;
    border-radius: 3rem;
    backdrop-filter:  blur(15px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-white);
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}
nav a.active{
    background: var(--color-bg);
    color:var(--color-white);
    box-shadow: 0 0 5px var(--color-primary);
}